.Fallback{top:10px;left:10px;font-family:'Fuzzy Bubbles', cursive;position:absolute;font-size:24px;z-index:1;border:black 2px solid;border-radius:15px;padding:5px;text-decoration:none;color:black}.Fallback-Image{width:25px}

.TestsHub{font-family:'Fuzzy Bubbles', cursive;margin:0 50px}.TestsHub-Header{margin-top:120px;font-size:64px;text-align:center;margin-bottom:60px}.TestsHub-List{text-align:center}.TestsHub-List-Test{text-decoration:none;color:black;vertical-align:bottom;border:black solid 4px;border-radius:20px;display:inline-block;height:350px;width:310px;margin:10px}.TestsHub-List-Test-Image{width:280px;border:black solid 2px;border-radius:5px;margin-top:10px}.TestsHub-List-Test-Header{margin:10px;font-size:26px}.TestsHub-List-Test-Description{margin:5px;font-size:22px}@media screen and (max-width: 769px){.TestsHub{margin:0}.TestsHub-Header{margin-top:20px}}

.Loading{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;justify-content:center;align-items:center}

.AccentTester{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-between;text-align:center;background-image:url(/tests-hub/static/media/bg.a0a70828.jpg);background-repeat:no-repeat;background-size:cover;font-family:'Roboto Slab', serif}.AccentTester-Header{margin-top:40;font-size:64px}.AccentTester-Workplace-Word{font-size:90px;font-family:'Bona Nova', serif;display:flex;justify-content:center;align-items:center}.AccentTester-Workplace-Word-Fragment-Vowel{display:block;margin:0;padding:0;border:none;background-color:transparent;cursor:pointer;font-size:90px;font-family:'Bona Nova', serif}.AccentTester-Workplace-Word-Congrats{font-size:60px}.AccentTester-Workplace-Stats{font-family:'Roboto Slab', serif;display:block;font-size:70px}.AccentTester-Text{text-align:center;color:gray;padding-bottom:50px;font-size:35px}@media screen and (min-width: 2000px){.AccentTester-Header{font-size:100px}.AccentTester-Workplace-Word{font-size:120px}.AccentTester-Workplace-Word-Fragment-Vowel{font-size:120px}.AccentTester-Workplace-Word-Fragment-Congrags{font-size:80px}.AccentTester-Text{font-size:60px}}@media screen and (max-width: 426px){.AccentTester-Header{margin-top:60px;font-size:32px}.AccentTester-Workplace-Word{font-size:60px}.AccentTester-Workplace-Word-Fragment-Vowel{font-size:60px}.AccentTester-Workplace-Word-Fragment-Congrags{font-size:24px}.AccentTester-Workplace-Stats{font-size:40px}.AccentTester-Text{font-size:16px}}.AccentTester .correct{color:green}.AccentTester .incorrect{color:red}

.TypingTester-Workplace-Stats{position:absolute;display:flex;background-color:rgba(0,0,0,0.582);top:0;left:0;width:100%;height:100%}.TypingTester-Workplace-Stats-Content{background-color:white;border:solid 3px black;border-radius:20px;margin:auto;font-family:'Roboto', sans-serif;font-size:32px;padding:30px;padding-left:60px;padding-right:60px}.TypingTester-Workplace-Stats-Content-Button{width:100%;height:30px}

.TypingTester-Workplace-Examples{width:100%;margin-top:50px;display:block;text-align:center}.TypingTester-Workplace-Examples-ButtonShow{font-size:20px}.TypingTester-Workplace-Examples-ButtonHide{font-size:20px}.TypingTester-Workplace-Examples-List-Example{width:100%;border:none;background-color:white;cursor:pointer;color:blue;text-decoration:underline;margin:10px 0;font-size:24px}

.TypingTester-MobileWarning{display:none}@media screen and (max-width: 769px){.TypingTester-MobileWarning{display:flex;justify-content:center;align-items:center;background-color:white;position:absolute;width:100%;height:100%;top:0;left:0}.TypingTester-MobileWarning-Content{text-align:center;font-family:'Roboto Slab', serif;font-size:32px;margin:10px}}

.TypingTester-Workplace-Text{margin-bottom:20px;padding:10px;width:100%;display:block;border:2px solid black;border-radius:20px;font-family:'Roboto', sans-serif;font-size:24px}.TypingTester-Workplace-Text-Completed{color:darkgray}.TypingTester-Workplace-Text-Error{background-color:red;color:white}

.TypingTester{position:absolute;top:0;left:0;width:100%;height:100%}.TypingTester-Workplace{margin:50px 20%}.TypingTester-Workplace-Texts-Input{display:block;padding:10px;width:100%;height:200px;border:2px solid black;border-radius:20px;resize:none;font-family:'Roboto Slab', serif;font-size:24px}.TypingTester-Workplace-Texts-Input :focus{overflow:hidden;outline:none}.TypingTester-Workplace-Texts-LoadButton{margin-top:20px;margin-left:20px;width:100px;height:50px}.TypingTester-Workplace-Texts-LoadInput{margin-left:20px}@media screen and (max-width: 769px){.TypingTester-Workplace{display:none}}

.CameraTester{position:absolute;top:0;left:0;width:100%;height:100%}.CameraTester-Video{width:100%;height:100%}

.ReactionTester{position:absolute;top:0;left:0;width:100%;height:100%;font-family:'Lato', sans-serif;color:#3d3d3d;font-size:50px}.ReactionTester .red{background-color:red !important}.ReactionTester-Buttons{height:70%;width:100%;display:flex;justify-content:center;align-items:center}.ReactionTester-Buttons-Button{background-color:#0c0;padding:80px 120px;border-radius:20px;text-align:center}.ReactionTester-ResultWindow{background-color:rgba(0,0,0,0.541);height:100%;width:100%;position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center}.ReactionTester-ResultWindow-InfoWindow{text-align:center;padding:15px 20px;margin-bottom:10%;background-color:white;border:solid black 4px;border-radius:20px;display:flex;align-content:center;justify-content:center;flex-direction:column}.ReactionTester-ResultWindow-InfoWindow-Button{margin:10px 20px;font-size:48px;display:block;border-radius:15px}@media screen and (max-width: 426px){.ReactionTester-Buttons{height:100%}.ReactionTester-Buttons-Button{font-size:32px;margin:0 20px;padding:90px 10px}.ReactionTester-ResultWindow-InfoWindow{font-size:32px;margin:0 10px}.ReactionTester-ResultWindow-InfoWindow-Button{font-size:32px;margin:15px 50px}}

body{margin:0}.hidden{display:none !important}.unselectable{-webkit-user-select:none;-webkit-touch-callout:none;-ms-user-select:none;user-select:none}

